.movie-list,.show-list{
    margin: 20px 0px;
}
h2{
    color: #79b8f3;
    margin-bottom: 10px;
    font-weight: 400;
    align-items: center;
    justify-content: center;
}
.movie-container{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(220px,1fr));
    grid-gap: 35px;
}
